import React, { useState } from "react";
import "./styles.css";

export default function App() {
  const [initialState, setState] = useState({
    productCost: 58,
    shippingCost: 5,
    transactionFee: 0.03,
    fixedCosts: 100,
    sellingPrice: 75,
    numberSold: 10,
    productExpenses: 0,
    shippingExpenses: 0,
    transactionExpenses: 0,
    totalExpenses: 0,
    revenue: 0,
    profit: 0
  });

  function updateCalculator(event: any) {
    event.preventDefault();

    const productExpenses = initialState.productCost * initialState.numberSold;
    const shippingExpenses =
      initialState.shippingCost * initialState.numberSold;

    const transactionExpenses = Math.ceil(
      initialState.sellingPrice *
        initialState.numberSold *
        initialState.transactionFee
    );

    const totalExpenses = Math.ceil(
      productExpenses +
        shippingExpenses +
        transactionExpenses +
        initialState.fixedCosts
    );

    const revenue = initialState.sellingPrice * initialState.numberSold;
    const profit = Math.ceil(revenue - totalExpenses);

    setState({
      ...initialState,
      [event.target.name]: event.target.value,
      productExpenses,
      shippingExpenses,
      transactionExpenses,
      totalExpenses,
      revenue,
      profit
    });
  }

  return (
    <div className="App">
      <h1>Dropshipping Calculator</h1>

      <div className="content">
        <div className="table-input">
          <table>
            <tr>
              <th>Product Cost</th>
              <th>Shipping Cost</th>
              <th>Transaction Fee</th>
              <th>Fixed Costs</th>
              <th>Selling Price</th>
              <th>Number Sold</th>
            </tr>
            <tr>
              <td>
                <input
                  type="number"
                  name="productCost"
                  value={initialState.productCost}
                  onChange={updateCalculator}
                  minLength={0}
                  maxLength={100000000}
                />
              </td>
              <td>
                <input
                  type="number"
                  name="shippingCost"
                  value={initialState.shippingCost}
                  onChange={updateCalculator}
                  minLength={0}
                  maxLength={100000000}
                />
              </td>
              <td>
                <input
                  type="number"
                  name="transactionFee"
                  value={initialState.transactionFee}
                  onChange={updateCalculator}
                  minLength={0}
                  maxLength={100000000}
                />
              </td>
              <td>
                <input
                  type="number"
                  name="fixedCosts"
                  value={initialState.fixedCosts}
                  onChange={updateCalculator}
                  minLength={0}
                  maxLength={100000000}
                />
              </td>
              <td>
                <input
                  type="number"
                  name="sellingPrice"
                  value={initialState.sellingPrice}
                  onChange={updateCalculator}
                  minLength={0}
                  maxLength={100000000}
                />
              </td>
              <td>
                <input
                  type="number"
                  name="numberSold"
                  value={initialState.numberSold}
                  onChange={updateCalculator}
                  minLength={0}
                  maxLength={100000000}
                />
              </td>
            </tr>
          </table>
        </div>
        <div className="table">
          <table>
            <tr>
              <th>Product Expenses</th>
              <th>Shipping Expenses</th>
              <th>Transaction Expenses</th>
              <th>Total Expenses</th>
              <th>Revenue</th>
              <th>Profit</th>
            </tr>
            <tr>
              <td>{initialState.productExpenses}</td>
              <td>{initialState.shippingExpenses}</td>
              <td>{initialState.transactionExpenses}</td>
              <td>{initialState.totalExpenses}</td>
              <td>{initialState.revenue}</td>
              <td>{initialState.profit}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  );
}
